.main {
  flex: 1;
  color: #ffffff;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
}

.gridIcon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgba(0,0,0, .125);
  display: flex;
  justify-content: center;
  align-items: center;
}
.gridTitle {
  font-size: 22px;
  font-weight: bold;
  margin-top: 5px;
}
.gridInfo {
  font-size: 12px;
  margin-top: 14px;
}
.yourImc {
  font-size: 16px;
  margin: 10px 0 50px 0;
}